  @media screen and (max-width: 561px) {
    .NavbarItems {
        position: relative;
      }

    .navbar-logo  {
      
        top: 0;
        left: 0;
        transform: translate(-60%, 0%);
        font-size: 1.4em;
      }

      .logo{
        width: 65px;
        height: 65px;
        border-radius: 50px;
       padding: 10px;
      }

      .nav-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 55vh;
        position: absolute;
        top: 80px;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
      }


  }



  @media screen and (max-width: 380px) {
    .NavbarItems {
        position: relative;
      }

    .navbar-logo  {
      
        top: 0;
        left: 0;
        transform: translate(-40%, 0%);
       
      }
      .logo{
        width: 60px;
        height: 60px;
        border-radius: 50px;
       padding: 10px;
      }

      .nav-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 60vh;
        position: absolute;
        top: 80px;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
      }


      .nav-links-mobile {
        display: block;
        text-align: center;
        margin: 2rem auto;
        border-radius: 4px;
        width: 55%;
        text-decoration: none;
        font-size: 1.5rem;
        background-color: transparent;
        color: #fff;
        padding: 14px 20px;
        border: 1px solid #fff;
        transition: all 0.3s ease-out;
      }



  }